<template>
  <div>
    <h1 class="my-4">Entra al mundo Geek!</h1>
    <div id="register">
      <img id="img" src="../assets/gamepad.png" class="mx-2" />
      <b-form @submit.prevent="registarUsuario()">
        <b-form-group label="Nombre Completo:" class="my-2">
          <b-form-input v-model="newUser.nombre_user" placeholder="Ingresa tu nombre completo"></b-form-input>
        </b-form-group>
        <b-form-group label="GameTag:" description="¿Como quieres ser reconocido en tu leyenda?">
          <b-form-input v-model="newUser.usuario_user" placeholder="Ingresa un nametag valido"></b-form-input>
        </b-form-group>
        <b-form-group
          label="Correo electronico:"
          description="Nunca compartiremos tus datos con terceros."
          class
        >
          <b-form-input
            v-model="newUser.email"
            required
            type="email"
            placeholder="Ingresa tu correo"
          ></b-form-input>
        </b-form-group>

        <b-form-group label="Contraseña:">
          <b-form-input
            v-model="newUser.password"
            type="password"
            placeholder="Ingresa una contraseña"
          ></b-form-input>
        </b-form-group>

        <b-button type="submit" variant="primary" class="mx-2 my-2">Comenzar leyenda</b-button>
        <b-button type="reset" variant="danger" :to="{name: 'Home'}">Cancelar</b-button>
      </b-form>
    </div>
  </div>
</template>

<script>
import router from "../router/index";
export default {
  name: "Register",
  data() {
    return {
      user: [],
      newUser: {
        nombre_user: "",
        usuario_user: "",
        password: "",
        email: "",
        role: "USER",
      },
    };
  },
  methods: {
    registarUsuario() {
      this.axios
        .post("/register", this.newUser)
        .then((res) => {
          this.user.push(res.data);
          (this.user.nombre_user = ""),
            (this.user.usuario_user = ""),
            (this.user.password = ""),
            (this.user.email = ""),
            (this.user.role = "USER");
          router.push({ name: "Login" });
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
};
</script>
<style>
#register {
  display: flex;
  justify-content: center;
}
#img{
  width: 350px;
  height: 350px;
  justify-content: center;
}
</style>